.gameInstructions {
  background-color: #34575c; 
  padding: 20px 20px;
  border-radius: 15px;
  margin-top: 140px;
  width: 100%;
  text-align: center;
  font-size: 5rem;
  color: rgb(241, 241, 241);
}

.gameInstructions:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}


