.card {
  background: #34575c;
  margin-left: 50px;
  border: 20px;
  border-color: black;
  border-radius: 10px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  -webkit-box-shadow: 0 6px 12px rgb(98, 98, 98), 0 3px 6px rgb(85, 85, 85);
          box-shadow: 0 6px 12px rgb(98, 98, 98), 0 3px 6px rgb(85, 85, 85);
  text-align: left;
}

.card > .img-container {
  border-radius: 10px;
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: -webkit-radial-gradient(#4cb5f5, #b7b8b6);
  background: -o-radial-gradient(#4cb5f5, #b7b8b6);
  background: radial-gradient(#4cb5f5, #b7b8b6);
}

.card > .img-container > img {
  width: 80%
}

.card > .img-container:hover {
  -webkit-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
          transform: rotate(360deg);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  background: -webkit-radial-gradient(#4cb5f5, white);
  background: -o-radial-gradient(#4cb5f5, white);
  background: radial-gradient(#4cb5f5, white);
}


